@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: bold;
  font-display: swap;
}
.page-header__logo {
  text-decoration: none;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.navigation {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  width: 320px;
}

.navigation__toggle {
  z-index: 1;
  position: relative;
  cursor: pointer;
  width: 48px;
  height: 48px;
  border: none;
  background-color: inherit;
}
.navigation__toggle::before {
  position: absolute;
  content: "";
  height: 4px;
  width: 22px;
  background: #81b3d2;
  left: 14px;
  top: 22px;
  box-shadow: 0 7px #81b3d2, 0 -7px #82b3d1;
}
.navigation__toggle:hover {
  opacity: 0.6;
}
.navigation__toggle:active {
  opacity: 0.3;
}

.navigation__toggle--cross {
  background-image: url("../img/icon-cross.svg");
}

.navigation__list {
  display: none;
  width: 320px;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #81b3d2;
  position: absolute;
  top: 0;
}

.navigation__list--opend {
  display: block;
}

.navigation__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.navigation__link {
  text-align: center;
  display: block;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #ffffff;
  padding: 17px 0 12px;
}

.navigation__list:last-child() {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.2) 100%);
}

@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }

  .page-header__logo {
    z-index: 1;
    position: absolute;
  }

  .navigation {
    text-align: center;
    position: static;
    width: 768px;
  }

  .navigation__list {
    position: inherit;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    width: 768px;
    background-color: #ffffff;
  }

  .navigation__item {
    min-width: 153px;
    box-shadow: none;
  }

  .navigation__item:nth-child(3) {
    grid-column: 4/5;
  }

  .navigation__link {
    padding: 18px auto 17px;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
  }
  .navigation__link:hover {
    color: #81b3d2;
  }
  .navigation__link:active {
    opacity: 0.3;
  }
  .navigation__link:focus {
    color: #766357;
  }

  .navigation__link--current {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .navigation__list {
    width: 1200px;
  }

  .navigation {
    width: 1200px;
  }

  .page-header__logo {
    left: auto;
  }
}
